



















































import Vue from 'vue';
import axios from 'axios' 

export default Vue.extend({
  name: 'Release',
  components: {
  },
  data: () => ({
    searchProductType: '',
    searchDocument: '',
    productTypeHeaders: [
      {
        text: 'Produkttyp',
        align: 'left',
        sortable: true,
        value: 'type.id',
      },
      { text: 'Version', value: 'version' },
    ],
    documentHeaders: [
      {
        text: 'Document',
        align: 'left',
        sortable: true,
        value: 'of.name',
      },
      { text: 'Version', value: 'version' },
    ],
    packets: [],
    packet: {},
  }),
  beforeRouteUpdate: function (to, from, next) {
    this.switchRelease(to.params.version)
    next()
  },
  mounted: function() {
    this.fetchData()
  },
  methods: {
    fetchData () {
      axios.get('/packets.json').then(response => {
        this.packets = response.data
        this.switchRelease(this.$route.params.version)
      })        
    },
    switchRelease (version: string) {
      for (let p of this.packets) {
        if ("version" in p && p["version"] == version) {
            this.packet = p
            break;
        }
      }

    }
  }
});
